.block {
    background-color: #545454;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    
    /* flex-wrap: wrap-reverse; */
}
/* h1 {
    margin: 0 auto;
} */
.wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 80%;
    padding: 100px 0;
    flex-wrap: wrap;
    z-index: 1;
}*, ::after, ::before {
    box-sizing: border-box;
}
.span {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    color: white;
    width: min-content;
}
.item {
    height: 400px;
    width: 300px;
    position: relative;
    background-color: rgb(0, 0, 0);
    content: "";
    top: 30px;
}
.span >span {
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
}
.span h1 span{
    position: relative;
    font-size: 50px;
    padding: 10px 0 10px;
    z-index: 1;
}
.span h1 span::after {
    content: "";
    position: absolute;
    left: 46px;
    right: 0;
    bottom: 23px;
    height: 13px;
    width: 80%;
    z-index: -1;
    background: chartreuse;
}
/* img {
   
    height: auto;
} */
.span p{
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.span h1 span {
    font-size: 50px;
    display: inline-block;
}

